/* NOTIFICATION */
.s_notification {
  display: block;
  position: fixed;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  top: 20%;
  right: 0;
  z-index: 10;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.s_notification i {
  margin-right: 15px;
  font-weight: bold;
}
.s_notification .error_message {
  padding: 10px;
  font-size: 12px;
  text-transform: uppercase;
  display: flex;
  background: #bd3630;
  color: whitesmoke;
  align-items: center;
  justify-content: center;
}
.s_notification .sucess_message {
  padding: 10px;
  font-size: 12px;
  text-transform: uppercase;
  display: flex;
  background: #4caf4f;
  color: whitesmoke;
  align-items: center;
  justify-content: center;
}

/* NOTIFICATION */
